    <p className="f-info">In Minecraft, there are two main game modes: Survival and Creative. In Survival mode, the goal…is to survive. Players appear in a new world and are challenged to use its resources to build shelter, find food, and craft tools to help them construct the universe they want to live in. In Survival, players have an inventory that consists of four rows of slots used to store items, nine of which are found on the hotbar. All blocks require a certain amount of time to break, unlike Creative mode where blocks can be instantly destroyed. These items can be used in recipes to create tools including (Pickaxe, Axe, Sword, Hoe, Shovel) and other items. The recipes can also be accessed easier by clicking the Recipe book in the bottom left corner. This process is known as crafting. Most crafting recipes require a crafting table. Some items cannot be obtained by crafting normally and require a furnace for processing (e.g. glass requires sand for smelting). Various other crafting stations exist for other uses, such as brewing, repairing, and enchanting.</p>

